import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  Divider,
  Form,
  Input,
  Button,
  Drawer,
  Space,
  Select,
  DatePicker,
} from "antd";
import moment, { min } from "moment";

import {
  isRepairDrawerVisible,
  RepairFilterInputChangeValue,
  getStockRepairList,
  setStockRepairParams,
} from "../../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

var date = new Date(),
  y = date.getFullYear(),
  m = date.getMonth();

const filter = (props) => {
  const [form] = Form.useForm();
  useEffect(() => {}, []);

  const onFinish = () => {
    props.setStockRepairParams({
      ...props.stockRepairparams,
      start_date: props.stockRepairparams.start_date,
      end_date: props.stockRepairparams.end_date,
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getStockRepairList({
      ...props.stockRepairparams,
      start_date:
        props.stockRepairparams.start_date != null &&
        props.stockRepairparams.start_date != ""
          ? moment(props.stockRepairparams.start_date).format("YYYY-MM-DD")
          : "",
      end_date:
        props.stockRepairparams.end_date != null &&
        props.stockRepairparams.end_date != ""
          ? moment(props.stockRepairparams.end_date)
              .add(1, "days")
              .format("YYYY-MM-DD")
          : "",
      page: 1,
      search: "",
      page_size: 10,
    });
    props.isRepairDrawerVisible(false);
  };

  function clearFilter() {
    props.setStockRepairParams({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });

    props.getStockRepairList({
      start_date: "",
      end_date: "",
      page: 1,
      search: "",
      page_size: 10,
    });
  }
  function disableDateRanges(range = { startDate: false, endDate: false }) {
    const { startDate, endDate } = range;
    return function disabledDate(current) {
      let startCheck = true;
      let endCheck = true;
      if (startDate) {
        startCheck =
          current &&
          current < moment(startDate + " 00:00:01", "YYYY-MM-DD HH:mm:ss");
      }
      if (endDate) {
        endCheck =
          current &&
          current >
            moment(
              moment(endDate).format("YYYY-MM-DD") + " 23:59:59",
              "YYYY-MM-DD HH:mm:ss"
            );
      }

      return (startDate && startCheck) || (endDate && endCheck);
    };
  }

  useEffect(() => {
    form.setFieldsValue({
      start_date: props.stockRepairparams?.start_date,
      end_date: props.stockRepairparams?.end_date,
    });
  }, [props.stockRepairparams]);

  function onSearch(val) {
    props.getStockRepairList({ page: 1, search: val, page_size: 10 });
  }
  return (
    <Row gutter={[24, 24]}>
      <Col span={24}>
        <Drawer
          title="Filters"
          width={420}
          onClose={() => {
            props.isRepairDrawerVisible(false);
          }}
          placement="right"
          visible={props.stockRepairDrawer}
          bodyStyle={{
            paddingBottom: 10,
          }}
        >
          <Form
            form={form}
            layout="vertical"
            name="basic"
            initialValues={{ remember: true }}
            onFinish={onFinish}
          >
            <Row gutter={[12, 12]}>
              <Col span={12}>
                <Form.Item label="From Date" name="start_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: new Date(),
                    })}
                    onChange={(date) => {
                      props.RepairFilterInputChangeValue({
                        key: "start_date",
                        value: date == null ? "" : date,
                      });
                      if (date == null) {
                        props.RepairFilterInputChangeValue({
                          key: "end_date",
                          value: "",
                        });
                      }
                    }}
                  />
                </Form.Item>
              </Col>

              <Col span={12}>
                <Form.Item label="To Date" name="end_date">
                  <DatePicker
                    style={{ width: "100%" }}
                    picker="date"
                    placeholder="Choose Date"
                    format="DD-MM-YYYY"
                    disabledDate={disableDateRanges({
                      endDate: moment().format("YYYY-MM-DD"),
                      startDate: moment(
                        props.stockRepairparams?.start_date
                      ).format("YYYY-MM-DD"),
                    })}
                    disabled={
                      props.stockRepairparams.start_date == null ||
                      props.stockRepairparams.start_date == ""
                        ? true
                        : false
                    }
                    onChange={(date) => {
                      props.RepairFilterInputChangeValue({
                        key: "end_date",
                        value: date == null ? "" : date,
                      });
                    }}
                  />
                </Form.Item>
              </Col>
            </Row>
            <Row gutter={[12, 12]}>
              <Col md={12}>
                <Button
                  block
                  type="secondary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    clearFilter();
                  }}
                  htmlType="button"
                >
                  Clear
                </Button>
              </Col>
              <Col md={12}>
                <Button
                  block
                  type="primary"
                  className="hp-submit-btn hp-mt-10"
                  onClick={() => {
                    form.submit();
                  }}
                >
                  Apply
                </Button>
              </Col>
            </Row>
          </Form>
        </Drawer>
      </Col>
    </Row>
  );
};

function mapStateToProps({ stockRequest, mini }) {
  return {
    stockRepairDrawer: stockRequest.stockRepairDrawer,
    stockRepairparams: stockRequest.stockRepairparams,
  };
}

export default connect(mapStateToProps, {
  getStockRepairList,
  isRepairDrawerVisible,
  setStockRepairParams,
  RepairFilterInputChangeValue,
})(filter);
