function mapStateToProps({ stockRequest, system }) {
  return {
    stockRepairViewModel: stockRequest.stockRepairViewModel,
    stockTransferRepairDetails: stockRequest.stockTransferRepairDetails,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  isRepairViewModelVisible,
  stockTransferRepair,
})(stockTransferRepairsModel);
