import React, { useEffect } from "react";
import { Card, Row, Col, Table, Space, Pagination, Popconfirm } from "antd";
import { connect } from "react-redux";
import Swal from "sweetalert2";
import { RiErrorWarningLine } from "react-icons/ri";
import {
  getStockTransferRepairById,
  isRepairViewModelVisible,
  isRepairModelVisible,
  stockRepairEdit,
  stockRepairDelete,
  getStockRepairList,
  setStockRepairParams,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import moment from "moment";

function StockTransferRepairsList(props) {
  const { stockRepairList } = props;

  useEffect(() => {
    // props.getStockRepairList({ ...props.stockRepairparams, page: 1 });
  }, []);

  function onPageChange(page, pageSize) {
    props.setStockRepairParams({
      ...props.stockRepairparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockRepairList({
      ...props.stockRepairparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const columnClass =
    "hp-p1-body hp-text-color-black-100 hp-text-color-dark-0 hp-font-weight-600 ";

  const columns = [
    {
      title: "S.No",
      width: "5%",
      dataIndex: "index",
    },
    {
      title: "Code",
      render: (row) => <span className={columnClass}>{row.code}</span>,
      width: "5%",
    },
    {
      title: "Mechanic Name",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.mechanicname}</span>,
    },
    {
      title: "Mechanic Mobile",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.mobile}</span>,
    },
    {
      title: "Out Time",
      width: "20%",
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.out_time).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Out Time",
      width: "20%",
      render: (row) => (
        <span className={columnClass}>
          {moment(row?.in_time).format("DD-MM-YYYY, h:mm:ss a")}
        </span>
      ),
    },
    {
      title: "Vehicle No",
      width: "10%",
      render: (row) => (
        <span className={columnClass}>{row.purchase?.vehicle?.vehicleno}</span>
      ),
    },
    {
      title: "Description",
      width: "10%",
      render: (row) => <span className={columnClass}>{row.description}</span>,
    },

    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
          <>
            <i
              className="ri-eye-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.getStockTransferRepairById(row.id);
                props.isRepairViewModelVisible(true);
              }}
            ></i>

            {row.in_time == null && (
              <>
                {props.userAccessList?.indexOf(
                  "StockTransfer.change_repairvechicle"
                ) !== -1 && (
                  <i
                    className="ri-edit-box-line hp-mr-2 "
                    style={{ cursor: "pointer", color: "#fc6f03" }}
                    onClick={() => {
                      props.stockRepairEdit(row);
                      props.isRepairModelVisible(true);
                    }}
                  ></i>
                )}

                {props.userAccessList?.indexOf(
                  "StockTransfer.delete_repairvechicle"
                ) !== -1 && (
                  <Popconfirm
                    placement="topRight"
                    title="Are you sure to delete this Record?"
                    onConfirm={() => props.stockRepairDelete(row.id)}
                    okText="Yes"
                    cancelText="No"
                    className="hp-ml-8"
                    icon={
                      <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
                    }
                  >
                    <i
                      className="ri-delete-bin-2-line "
                      style={{ cursor: "pointer", color: "#ff0000" }}
                    ></i>
                  </Popconfirm>
                )}
              </>
            )}
          </>
        </Space>
      ),
    },
  ];

  const data = stockRepairList.map((row, key) => ({
    ...row,
    // index: key + 1,
    index:
      (props.stockRepairparams.page - 1) * props.stockRepairparams.page_size +
      (key + 1),
  }));

  return (
      <Row className="hp-mb-12">
        <Col span={24}>
          <Table
            columns={columns}
            rowClassName="hp-btn-bg-color-none"
            dataSource={data}
            scroll={{ x: 500 }}
            pagination={false}
            size="middle"
          />
        </Col>
      </Row>
  );
}

function mapStateToProps({ stockRequest, system }) {
  return {
    noofpages: stockRequest.noofpages,
    stockRepairlistCount: stockRequest.stockRepairlistCount,
    stockRepairViewModel: stockRequest.stockRepairViewModel,
    stockRepairList: stockRequest.stockRepairList,
    addRepairModel: stockRequest.addRepairModel,
    stockRepairparams: stockRequest.stockRepairparams,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getStockTransferRepairById,
  isRepairViewModelVisible,
  isRepairModelVisible,
  stockRepairEdit,
  stockRepairDelete,
  getStockRepairList,
  setStockRepairParams,
})(StockTransferRepairsList);
