import { Fragment, useEffect } from "react";

import { connect } from "react-redux";
import "flatpickr/dist/themes/light.css";
import { RiCloseFill } from "react-icons/ri";
import { Button, Input, Form, Modal, Row, Col, Select } from "antd";
import { RiInformationLine } from "react-icons/ri";
import NumericInput from "../../../../components/data-entry/inputs/numberInput";
import {
  isRepairModelVisible,
  RepairInputChangeValue,
  stockRepairAdd,
  getPurchaseById,
  getFormulaExecuter,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";

import {
  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
} from "../../../../../store/mini/miniSlice";

const addRepair = (props) => {
  const [form] = Form.useForm();
  const { Option } = Select;

  useEffect(() => {
    props.getPurchaseAvailableStock({ ...props.purchaseAvailableStockParams });
  }, []);

  function ChangeHandeler(e) {
    props.RepairInputChangeValue({
      key: e.target.name,
      value: e.target.value,
    });
  }

  function vehicleDetails(purchaseId) {
    props.purchaseAvaliableStockList.forEach((purchase) => {
      if (purchase.id == purchaseId) {
        props.getPurchaseById(purchase);
      }
    });
  }
  // Adds New Country
  const handleAdd = () => {
    const obj = {
      id: 0,
      mechanicname: props.repairData?.mechanicname,
      mobile: props.repairData?.mobile,
      purchase_id: props.repairData?.purchase_id,
      description: props.repairData?.description,
    };
    props.stockRepairAdd(obj);
  };
  // Updates Country in Store
  const handleUpdate = () => {
    const obj = {
      id: props.repairData?.id,
      mechanicname: props.repairData?.mechanicname,
      mobile: props.repairData?.mobile,
      purchase_id: props.repairData?.purchase_id,
      description: props.repairData?.description,
    };
    props.stockRepairAdd(obj);
  };

  // Reset Input Values on Close

  const handleResetInputValues = () => {
    form.resetFields();
    props.isRepairModelVisible(false);
  };

  const FooterAddOrUpdate = () => {
    if (props.repairData.id == null || props.repairData.id == 0) {
      return (
        <Fragment>
          <Button
            onClick={() => {
              form.submit();
            }}
            type="primary"
            block
          >
            Add
          </Button>
        </Fragment>
      );
    } else {
      return (
        <Fragment>
          <Button
            type="primary"
            onClick={() => {
              form.submit();
            }}
            block
          >
            Update
          </Button>
        </Fragment>
      );
    }
  };

  const FooterCancel = () => {
    return (
      <Fragment>
        <Button
          onClick={() => {
            handleResetInputValues();
          }}
          type="primary"
          block
          ghost
        >
          Cancel
        </Button>
      </Fragment>
    );
  };

  const modalTitle = (
    <h5 className="modal-title">
      {props.repairData.id == null || props.repairData.id == 0
        ? "Add"
        : "Update"}{" "}
      Repair
    </h5>
  );

  useEffect(() => {
    form.setFieldsValue({
      mechanicname: props.repairData.mechanicname,
      mobile: props.repairData.mobile,
      purchase_id: props.repairData.purchase_id,
      vehicleno: props.purchaseDetails?.vehicle?.vehicleno,
      description: props.repairData.description,
    });
  }, [props.repairData]);

  return (
    <Modal
      visible={props.addRepairModel}
      title={modalTitle}
      onCancel={() => {
        handleResetInputValues();
        form.resetFields();
        props.isRepairModelVisible(false);
      }}
      className="hp-modal-p-24"
      footer={
        <Row justify="center">
          <Col span={12} className="hp-pr-4">
            <FooterCancel />
          </Col>

          <Col span={12} className="hp-pl-4">
            <FooterAddOrUpdate />
          </Col>
        </Row>
      }
      centered
      closeIcon={
        <RiCloseFill className="remix-icon text-color-black-100" size={24} />
      }
      width={616}
    >
      <Form
        layout="vertical"
        form={form}
        onFinish={() => {
          if (props.repairData.id == null) {
            handleAdd();
          } else {
            handleUpdate();
          }
        }}
      >
        <Row>
          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Mechanic Name"
              name="mechanicname"
              rules={[
                {
                  required: true,
                  message: "Please Input Mechanic Name",
                },
                {
                  pattern: new RegExp(/^[a-zA-Z][a-zA-Z\s]+$/i),
                  message: "field does not accept numbers",
                },
              ]}
              hasFeedback
            >
              <Input
                id="mechanicname"
                name="mechanicname"
                style={{ width: "100%" }}
                placeholder="Mechanic Name"
                value={props.repairData.mechanicname}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Mobile Number"
              name="mobile"
              rules={[
                {
                  required: true,
                  message: "Please Input Mobile Number",
                },
              ]}
              getValueProps={() => {}}
              hasFeedback
            >
              <NumericInput
                id="mobile"
                name="mobile"
                maxLength={10}
                style={{ width: "100%" }}
                placeholder="Mobile Number"
                value={
                  props.repairData.mobile == null
                    ? ""
                    : props.repairData?.mobile
                }
                onChange={props.RepairInputChangeValue}
              />
            </Form.Item>
          </Col>
          {props.repairData.id == null ? (
            <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
              <Form.Item
                label="Vehicle Number"
                name="vehicle_id"
                rules={[
                  {
                    required: true,
                    message: "Please Input Vehicle Number",
                  },
                ]}
                hasFeedback
              >
                <Select
                  // disabled={
                  //   props.repairData.purchase_id == null ? false : true
                  // }
                  showSearch
                  placeholder="Select vehicle "
                  optionFilterProp="vehicle"
                  filterOption={false}
                  onSearch={(value) => {
                    props.setPurchaseAvailableStockParams({
                      ...props.purchaseAvailableStockParams,
                      page: 1,
                      search: value,
                    });
                    props.getPurchaseAvailableStock({
                      ...props.purchaseAvailableStockParams,
                      page: 1,
                      search: value,
                    });
                  }}
                  onChange={(value) => {
                    vehicleDetails(value);
                    props.RepairInputChangeValue({
                      key: "purchase_id",
                      value: value,
                    });
                  }}
                  onPopupScroll={() => {
                    if (
                      props.purchaseAvailableStockParams.page <
                      props.purchaseAvailableStockParams.no_of_pages
                    ) {
                      props.setPurchaseAvailableStockParams({
                        ...props.purchaseAvailableStockParams,
                        page: props.purchaseAvailableStockParams.page + 1,
                      });
                      props.getPurchaseAvailableStock({
                        ...props.purchaseAvailableStockParams,
                        page: props.purchaseAvailableStockParams.page + 1,
                      });
                    }
                  }}
                  value={
                    props.repairData.vehicle_id == null
                      ? ""
                      : props.repairData?.vehicle_id
                  }
                >
                  {props.purchaseAvaliableStockList.map((purchase, index) => {
                    return (
                      <Option key={index} value={purchase.id}>
                        {purchase?.vehicle.vehicleno} - {purchase?.code}
                      </Option>
                    );
                  })}
                </Select>
              </Form.Item>
            </Col>
          ) : (
            <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
              <Form.Item label="Vehicle Number" name="vehicleno">
                <Input
                  id="vehicleno"
                  name="vehicleno"
                  style={{ width: "100%" }}
                  placeholder="Vehicle Number"
                  value={props.purchaseDetails?.vehicle?.vehicleno}
                  disabled
                />
              </Form.Item>
            </Col>
          )}

          <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Vehicle Info">
              <Input
                id="vehicleVersion"
                name="vehicleVersion"
                style={{ width: "100%" }}
                placeholder="Vehicle Info"
                value={props.purchaseDetails?.vehicle?.version?.name}
                disabled
              />
            </Form.Item>
          </Col>

          {/* <Col md={12} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item label="Purchase Amount">
              <Input
                id="purchase"
                name="purchase"
                style={{ width: "100%" }}
                placeholder="Purchase Amount"
                value={props.purchaseDetails?.purchaserate}
                disabled
              />
            </Form.Item>
          </Col> */}

          <Col md={24} xs={24} className="hp-pr-sm-0 hp-pr-12">
            <Form.Item
              label="Remarks"
              name="description"
              rules={[
                {
                  required: true,
                  message: "Please Input Description",
                },
              ]}
              hasFeedback
            >
              <Input.TextArea
                id="description"
                name="description"
                style={{ width: "100%" }}
                placeholder="Write Remarks..."
                value={props.repairData.description}
                onChange={ChangeHandeler}
              />
            </Form.Item>
          </Col>
        </Row>
      </Form>
    </Modal>
  );
};

function mapStateToProps({ stockRequest, mini }) {
  return {
    repairData: stockRequest.repairData,
    addRepairModel: stockRequest.addRepairModel,

    purchaseDetails: stockRequest.purchaseDetails,
    // minSaleAmount: stockRequest.minSaleAmount,
    // maxSaleAmount: stockRequest.maxSaleAmount,
    purchaseAvaliableStockList: mini.purchaseAvaliableStockList,
    purchaseAvailableStockParams: mini.purchaseAvailableStockParams,
  };
}

export default connect(mapStateToProps, {
  isRepairModelVisible,
  RepairInputChangeValue,
  stockRepairAdd,

  setPurchaseAvailableStockParams,
  getPurchaseAvailableStock,
  getPurchaseById,
  getFormulaExecuter,
})(addRepair);
