import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  Row,
  Col,
  List,
  Empty,
  Button,
  Input,
  Card,
  Pagination,
  Spin,
} from "antd";
import { Search } from "react-iconly";
import {
  OrderedListOutlined,
  AppstoreOutlined,
  PlusOutlined,
  FilterOutlined,
  UploadOutlined,
} from "@ant-design/icons";
import PageTitle from "../../../../../layout/components/content/page-title";
import EmptyImage from "../../../../../assets/images/app/empty-list.svg";
import { isMobile } from "react-device-detect";
import {
  getStockRepairList,
  setStockRepairParams,
  isRepairModelVisible,
  isRepairDrawerVisible,
} from "../../../../../store/sideMenu/StockTransfer/StockRequest/stockrequestSlice";
import { getReportList } from "../../../../../store/reports/reportsSlice";
import StockTransferRepairsGrid from "./StockTransferRepairsGrid";
import StockTransferRepairsModel from "./StockTransferRepairsModel";
import StockTransferRepairsList from "./StockTransferRepairsList";
import AddRepair from "./addRepair";
import Filter from "./Filters/Filter";

function stockTransferRepairs(props) {
  const { Search } = Input;

  useEffect(() => {
    props.getStockRepairList({
      ...props.stockRepairparams,
      page: 1,
      search: "",
      page_size: 10,
    });
    props.setStockRepairParams({
      ...props.stockRepairparams,
      page: 1,
      search: "",
      page_size: 10,
    });
  }, []);

  const [productListToggle, setProductListToggle] = useState(false);

  function onPageChange(page, pageSize) {
    props.setStockRepairParams({
      ...props.stockRepairparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getStockRepairList({
      ...props.stockRepairparams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.setStockRepairParams({ ...props.stockRepairparams, search: val });
    props.getStockRepairList({ ...props.stockRepairparams, search: val });
  }

  function showTotal(total) {
    return "Total " + total + " items";
  }

  const onChange = (key) => {
    setActiveTab(key);
    if (key == "1") {
      props.getStockRepairList({
        page: 1,
        search: "",
        page_size: 10,
      });
    } else {
      props.getStockRepairList({
        page: 1,
        search: "",
        page_size: 10,
      });
    }
    console.log(key);
  };

  // Export Functionality

  function exportData() {
    // var { file_format, ...params } = props.reportParamsData;
    var objData = {
      file_format: 0,
      model_name: "RepairVechicle",
    };

    props.getReportList({
      objData,
      ParamsData: {
        ...props.stockRepairparams,
        start_date:
          props.stockRepairparams.start_date != null &&
          props.stockRepairparams.start_date != ""
            ? moment(props.stockRepairparams.start_date).format("YYYY-MM-DD")
            : "",
        end_date:
          props.stockRepairparams.end_date != null &&
          props.stockRepairparams.end_date != ""
            ? moment(props.stockRepairparams.end_date)
                .add(1, "days")
                .format("YYYY-MM-DD")
            : "",
      },
    });
  }

  return (
    <Row gutter={[12, 12]}>
      <PageTitle pageTitle="Stock Transfer Repairs" goBack={false} />

      <Col span={24}>
        <Row gutter={[12, 12]} className="hp-ecommerce-app-header ">
          {/* <Col md={12} xs={24} className="hp-ecommerce-app-header-search "> */}
          <Col span={24} xs={24} sm={24} md={
              props.userAccessList?.indexOf("Purchase.export_purchase") !== -1
                ? 12
                : 15
            }
            className="hp-ecommerce-app-header-search "
          >
            <Search
              placeholder="Search for date, doc, number, customer, counter, vehicle"
              allowClear
              onChange={(e) => {
                if (e.target.value == "") {
                  props.getStockRepairList({
                    ...props.stockRepairparams,
                    page: 1,
                    search: "",
                  });
                  props.setStockRepairParams({
                    ...props.stockRepairparams,
                    page: 1,
                    search: "",
                  });
                }
              }}
              onSearch={(value) => {
                onSearch(value);
              }}
            />
          </Col>
          <Col span={24} xs={4} sm={4}  md={3}>
            <Button
              onClick={() => {
                props.isRepairDrawerVisible(true);
              }}
              className="hp-w-100 hp-p-10"
              icon={<FilterOutlined className="hp-mr-10" />}
            >
             {!isMobile && "Filter"}
            </Button>
          </Col>

          {props.userAccessList?.indexOf("StockTransfer.add_repairvechicle") !==
            -1 && (
            <Col span={24} xs={4} sm={4} md={4}>
              <Button
                onClick={() => {
                  props.isRepairModelVisible(true);
                }}
                className="hp-w-100 hp-p-10"
                icon={<PlusOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Add Repair"}
              </Button>
            </Col>
          )}

          {props.userAccessList?.indexOf(
            "StockTransfer.export_repairvechicle"
          ) !== -1 && (
            <Col span={24} xs={4} sm={4}  md={3}>
              <Button
                onClick={() => {
                  exportData();
                }}
                className="hp-w-100 hp-p-10"
                icon={<UploadOutlined className="hp-mr-10" />}
              >
                {!isMobile && "Export"}
              </Button>
            </Col>
          )}

          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<AppstoreOutlined />}
              onClick={() => setProductListToggle(false)}
            ></Button>
          </Col>

          <Col span={24} xs={6} sm={5} md={1}>
            <Button
              className="hp-w-100 hp-p-12"
              icon={<OrderedListOutlined />}
              onClick={() => setProductListToggle(true)}
            ></Button>
          </Col>
        </Row>
      </Col>

      <Col
        span={24}
        xs={24}
        className="hp-purchase-app-content"
        productListToggle={productListToggle}
        setProductListToggle={setProductListToggle}
      >
        <Card>
          <Spin tip="loading" spinning={props.loading} delay={500}>
            {props.stockRepairList.length !== 0 ? (
              <>
                {!productListToggle ? (
                  <>
                    <List
                      dataSource={props.stockRepairList}
                      renderItem={(value) => (
                        <StockTransferRepairsGrid value={value} />
                      )}
                    />
                    <Pagination
                      size="small"
                      current={props.stockRepairparams?.page}
                      pageSize={props.stockRepairparams?.page_size}
                      total={props.stockRepairlistCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                ) : (
                  <>
                    <StockTransferRepairsList
                      stockTransferList={props.stockRepairList}
                      // deletePurchase={deleteRecord}
                    />{" "}
                    <Pagination
                      size="small"
                      current={props.stockRepairparams?.page}
                      pageSize={props.stockRepairparams?.page_size}
                      total={props.stockRepairlistCount}
                      showTotal={showTotal}
                      showSizeChanger
                      onChange={onPageChange}
                      className="hp-mb-12 hp-mt-24 hp-float-right"
                    />
                  </>
                )}
              </>
            ) : (
              <Card>
                <Empty
                  className="hp-m-32"
                  image={EmptyImage}
                  imageStyle={{
                    height: 160,
                  }}
                  description={
                    <h5>Your Stock Transfer Repair list is empty</h5>
                  }
                ></Empty>
              </Card>
            )}
          </Spin>
        </Card>
      </Col>
      <StockTransferRepairsModel />
      <AddRepair />
      <Filter />
    </Row>
  );
}
function mapStateToProps({ stockRequest, system }) {
  return {
    stockRepairList: stockRequest.stockRepairList,
    stockRepairparams: stockRequest.stockRepairparams,
    noofpages: stockRequest.noofpages,
    stockRepairlistCount: stockRequest.stockRepairlistCount,
    loading: stockRequest.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getStockRepairList,
  setStockRepairParams,
  isRepairModelVisible,
  isRepairDrawerVisible,
  getReportList
})(stockTransferRepairs);
